	module.exports = {"data":{"site":{"siteMetadata":{"title":"Return of the Shadow"}},"markdownRemark":{"id":"/home/kvalencik/git/rots-site/src/pages/news/001616.md absPath of file >>> MarkdownRemark","html":"<blockquote>\n<p>Message 1616 : Wed Sep 28 (Blent)      :: Mob Revamp: Enormous, Bristly Spider</p>\n</blockquote>\n<p>As a project to re-acquaint myself with the scripting and building\nsystems after an extended period of absence, I rewrote almost everything\nconcerned with the enormous, bristly spider.</p>\n<p>Basically:</p>\n<p>He’s less threatening before he retreats into his web.\nHis web is hard to move around in now.\nGetting cocooned is a bit more, uh, scary. And disrupting to the fight.</p>\n<p>And you get in return:</p>\n<p>New items! Three, to be exact (the butcher remains unchanged); things\nfor several different classes (and one item exclusively for dark-side\ncharacters).</p>\n<p>Also, I fixed a known bug with the spider: previously, when he retreated\nup into his web, you had to be quick to hit him, or else he would\ndisappear (instead of going back down to wait underneath the web).</p>\n<p>Now, he correctly returns to the area beneath his web if he has not been</p>\n<p>damaged.</p>\n<p>And, some other changes in the underground streambed:</p>\n<p>The load rate for both this spider and the serpent have been increased.\nThe dodge bonus on the black-scaled shield has been increased.</p>\n<p>I think that’s about it. Feedback is welcome regarding the new version\nof the enormous, bristly spider, especially concerning difficulty, reward\nrates, and problems/bugs. Send me a game mail.</p>\n<p>Blent</p>","frontmatter":{"title":"News 1616: Mob Revamp: Enormous, Bristly Spider","date":"October 09, 2017"}}},"pathContext":{"path":"/news/1616"}}

